$color: steelblue;

.my-flix {
    color: $color;
    font-family: Arial, sans-serif;
    margin: 20px;
    padding: 10px;
    border: 2px solid $color;
    width: 300px;
    border-radius: 5px;
    background-color: lighten($color, 40%);
    text-align: center;
    h1 {
        font-size: 2em;
        margin-bottom: 10px;
    }
}